import React, { Component } from "react";
import SkillBar from "react-skillbars";

import "./About.css";
import { Row, Col, Alert, Modal, Button } from "react-bootstrap";

class About extends Component {
  state = {
    collapsed: true,
    SKILLS: [
      { type: "HTML", level: 99 },
      { type: "CSS", level: 98 },
      { type: "JavaScript", level: 87 },
      { type: "jQuery", level: 92 },
      { type: "BootStrap", level: 90 },
      { type: "Photoshop", level: 100 },
      { type: "Angular.js", level: 16 },
      { type: "React.js", level: 25 },
      { type: "PHP", level: 36 },
      { type: "Ruby", level: 11 }
    ],
    skillcolours: {
      bar: "#00A1A7",
      title: {
        text: "#fff",
        background: "#04C2C9"
      }
    }
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({ collapsed: false });
    }, 5000);
  }

  //   handleClose = () => {
  //     this.setState({ test: 80 });
  //   };

  render() {
    const { SKILLS, skillcolours } = this.state;
    return (
      <div>
        <div className="title" />
        <p className="titletext">About</p>
        <div />
        <Row>
          <Col md={6}>
            <div className="userimgdiv">
              <div>
                <img
                  className="userimg"
                  src="https://dummyimage.com/600x400/04c2c9/f2f3ff"
                />
                <div className="descriptionwrapper">
                  <div className="descriptiondiv">
                    <h3 style={{ textAlign: "center", color: "#616161" }}>
                      Whose This Guy?
                    </h3>
                    <p className="desctext">
                      I am a passionate computer engineer and full-stack
                      developer with more than two years of experience. I have
                      good problem-solving skills and always strive to solve
                      complex real-world problems. I love to work on new
                      technologies and enhance my skill set.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </Col>
          <Col md={6}>
            <SkillBar skills={SKILLS} colors={skillcolours} height={22} />
            {/* <div className={`container ${collapsed ? "collapsed" : ""}`}>
              {SKILLS.map((data1, i) => {
                return (
                  <div key={i} className="skillchartdiv">
                    <div className="containerchart">
                      <div
                        className="skills"
                        style={{ width: `${data1.level}%` }}
                      >
                        <p className="skillname">{data1.type}</p>
                        <p className="exp">{data1.level}</p>
                      </div>
                    </div>
                    <p />
                  </div>
                );
              })}
            </div> */}
          </Col>
        </Row>
      </div>
    );
  }
}

export default About;



