import React, { Component } from "react";
import { Row, Col, Alert, Modal } from "react-bootstrap";
import "./Edu.css";

class Edu extends Component {
  render() {
    return (
      <div>
        <div className="title" />
        <p className="titletext">Academics</p>

        <div className="customdiv">
          <Row>
            <Col md={4} xs={12}>
              <img
                className="userimg"
                src="https://dummyimage.com/600x400/04c2c9/f2f3ff"
              />
            </Col>
            <Col md={8} xs={12}>
              <div style={{ textAlign: "center" }}>
                <b>St Francis Institute Of Technology</b>
                <p>B.E in Computer Science </p>
                <p> June 2013 - August 2018 </p>
              </div>
            </Col>
          </Row>
        </div>

        <div />
      </div>
    );
  }
}
export default Edu;



