import React, { Component, useState } from "react";
import "./Project.css";
import { Button } from "react-bootstrap";
import { Row, Col, Alert, Modal } from "react-bootstrap";
import Slider from "react-animated-slider";
import "react-animated-slider/build/horizontal.css";

class Project extends Component {
  state = {
    iamgeurl: [
      {
        show: false,
        img:
          "https://scontent.cdninstagram.com/vp/c901501a6ff05ced2b6561154ee1b4b0/5DCA3233/t51.2885-15/e35/p320x320/66341335_1160829987433118_4814764181849262029_n.jpg?_nc_ht=scontent.cdninstagram.com"
      },
      { img: "https://dummyimage.com/600x400/04c2c9/f2f3ff" },
      { img: "https://dummyimage.com/600x400/04c2c9/f2f3ff" },
      { img: "https://dummyimage.com/600x400/04c2c9/f2f3ff" },
      { img: "https://dummyimage.com/600x400/04c2c9/f2f3ff" },
      { img: "https://dummyimage.com/600x400/04c2c9/f2f3ff" },
      { img: "https://dummyimage.com/600x400/04c2c9/f2f3ff" }
    ]
  };

  handleShow = () => {
    this.setState({ show: true });
  };
  handleClose = () => {
    this.setState({ show: false });
  };
  render() {
    const { iamgeurl, show } = this.state;
    const divStyle = src => ({
      backgroundImage: "url(" + src + ")"
    });
    const customstyle = {
      display: "contents"
    };
    return (
      <div>
        <div className="title" />
        <p className="titletext">Portfolio</p>
        <div />
        <Row className="galleryrow">
          {iamgeurl.map((data, i) => {
            return (
              <Col md={4} key={i} style={{ paddingRight: 0, paddingLeft: 0 }}>
                <div className="gallery1div">
                  <div className="image1" style={divStyle(data.img)} />
                  <div className="imgoverlay">
                    <div className="buttonmaindiv">
                      <p>VelVett HomeStays</p>
                      <p> Angular 7/ Django </p>

                      <div
                        style={{ marginLeft: "30%" }}
                        className="custombutton"
                        onClick={this.handleShow}
                      >
                        <p> Learn More</p>
                      </div>
                    </div>
                  </div>
                </div>
              </Col>
            );
          })}
        </Row>

        {/* MODAL */}
        <Modal onHide={this.handleClose} size="lg" show={show}>
          <Modal.Body style={{ padding: 0 }}>
            <div>
              <Slider duration="1800">
                {iamgeurl.map((article, index) => (
                  <div
                    key={index}
                    className="image1"
                    style={divStyle(article.img)}
                  />
                ))}
              </Slider>
            </div>
            <div className="descriptiondiv">
              <p>Velvett Homestays</p>
              <hr />
              <p>Description</p>
            </div>
            <div />
          </Modal.Body>
          <Modal.Footer>
            <div className="custombutton" onClick={this.handleClose}>
              <p> Close</p>
            </div>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default Project;



