import React, { Component } from "react";
import "./Home.css";

class Home extends Component {
  state = {
    tasks: 0
  };

  componentDidMount() {
    let { tasks } = this.state;

    var scene = document.getElementById("scene");
    scene.width = window.innerWidth;
    scene.height = window.innerHeight;
    var sceneCtx = scene.getContext("2d");

    //Helper function for adding text in laptop
    function typeOut(str, startX, startY, lineHeight, padding) {
      "use strict";
      var cursorX = startX || 0;
      var cursorY = startY || 0;
      var lineHeight = lineHeight || 32;
      padding = padding || 10;
      var i = 0;
      var $_inter = setInterval(function() {
        var rem = str.substr(i);
        var space = rem.indexOf(" ");
        space = space === -1 ? str.length : space;
        var wordwidth = sceneCtx.measureText(rem.substring(0, space)).width;
        var w = sceneCtx.measureText(str.charAt(i)).width;
        if (cursorX + wordwidth >= canvas.width - padding) {
          cursorX = startX;
          cursorY += lineHeight;
        }
        sceneCtx.fillText(str.charAt(i), cursorX, cursorY);
        i++;
        cursorX += w;
        if (i === str.length) {
          clearInterval($_inter);
        }
      }, 75);
    }

    // Adding image of user
    function make_base() {
      let base_image = new Image();
      base_image.crossOrigin = "anonymous";
      base_image.src =
        "http://rippedcoders.com:8000/Media/acad/201908/10/typing.png";
      base_image.addEventListener("load", function() {
        sceneCtx.drawImage(
          base_image,
          scene.width / 2 - 250,
          scene.height / 2 - 80
        );
      });
      addIntroText();
    }
    make_base();

    function addIntroText() {
      sceneCtx.strokeStyle = "";
      sceneCtx.fillStyle = "white";
      sceneCtx.font = "12px sans-serif";
      var str = "Hello! I'm  IAN DSOUZA, I'm  a Full Stack Developer . . .";
      typeOut(
        str,
        scene.width / 2 - 200,
        scene.height / 2 - 130,
        32,
        scene.width / 2 - 200
      );
    }

    // creation on laptop
    function vscodebutton(x, y, radius, color) {
      sceneCtx.beginPath();
      sceneCtx.fillStyle = color;
      sceneCtx.arc(x, y, radius, 0, Math.PI * 2, false);

      sceneCtx.fill();
      sceneCtx.closePath();
    }

    //helper function to create  laptop rectangles
    function roundedRect(ctx, x, y, width, height, radius) {
      ctx.beginPath();
      ctx.moveTo(x, y + radius);
      ctx.lineTo(x, y + height - radius);
      ctx.arcTo(x, y + height, x + radius, y + height, radius);
      ctx.lineTo(x + width - radius, y + height);
      ctx.arcTo(x + width, y + height, x + width, y + height - radius, radius);
      ctx.lineTo(x + width, y + radius);
      ctx.arcTo(x + width, y, x + width - radius, y, radius);
      ctx.lineTo(x + radius, y);
      ctx.arcTo(x, y, x, y + radius, radius);
      ctx.stroke();
    }

    sceneCtx.fillStyle = "#1B242F";
    sceneCtx.fillRect(scene.width / 2 - 210, scene.height / 2 - 220, 420, 322);
    sceneCtx.closePath();

    sceneCtx.fillStyle = "#575a5b";
    sceneCtx.fillRect(scene.width / 2 - 210, scene.height / 2 - 220, 420, 25);
    sceneCtx.closePath();

    // buttons on  editor
    vscodebutton(scene.width / 2 - 200, scene.height / 2 - 206, 7, "#f44336");
    vscodebutton(scene.width / 2 - 180, scene.height / 2 - 206, 7, "#fbc02d");
    vscodebutton(scene.width / 2 - 160, scene.height / 2 - 206, 7, "#00c853");

    sceneCtx.fillStyle = "white";
    roundedRect(
      sceneCtx,
      scene.width / 2 - 225,
      scene.height / 2 - 240,
      450,
      350,
      15
    );
    roundedRect(
      sceneCtx,
      scene.width / 2 - 225,
      scene.height / 2 - 240,
      451,
      351,
      15
    );
    roundedRect(
      sceneCtx,
      scene.width / 2 - 225,
      scene.height / 2 - 240,
      452,
      352,
      15
    );
    roundedRect(
      sceneCtx,
      scene.width / 2 - 225,
      scene.height / 2 - 240,
      453,
      353,
      15
    );
    roundedRect(
      sceneCtx,
      scene.width / 2 - 225,
      scene.height / 2 - 240,
      454,
      354,
      15
    );
    sceneCtx.fillStyle = "white";

    sceneCtx.beginPath();
    sceneCtx.beginPath();
    sceneCtx.arc(
      scene.width / 2,
      scene.height / 2 + 100,
      80,
      0.3,
      0.9 * Math.PI,
      "",
      true
    );
    sceneCtx.fillStyle = "";
    sceneCtx.lineWidth = 2;
    sceneCtx.fill();
    sceneCtx.closePath();
    // creation on laptop end

    // Animation canvas begins
    var canvas = document.querySelector("canvas");
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    var c = canvas.getContext("2d");

    addEventListener("resize", () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      scene.width = window.innerWidth;
      scene.height = window.innerHeight;
      // init();
      // animate();
    });

    const colorArray = [
      "#2185C5",
      "#7ECEFD",
      "#FFF6EF",
      "#FF7F66",
      "#e16363",
      "#943855",
      "#fce2ae",
      "#b2fcff",
      "#fbc99d"
    ];
    const code = [
      "dev",
      "test",
      "bugs",
      "clients",
      "prod",
      "deadline",
      "lastdate",
      "git clone",
      "cd -r rm",
      "sudo"
    ];
    let mouse = {
      x: 10,
      y: 10
    };

    addEventListener("mousemove", function(event) {
      mouse.x = event.clientX;
      mouse.y = event.clientY;
    });

    function randomIntFromRange(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min);
    }

    function randomColors(colors) {
      return colors[Math.floor(Math.random() * colors.length)];
    }

    //task board
    function drawBoard() {
      //outer brown color
      sceneCtx.fillStyle = "#a87b4f";
      sceneCtx.fill();
      sceneCtx.fillRect(canvas.width - 360, 80, 300, 238);
      console.log("can", canvas.width - 360);
      sceneCtx.clearRect(canvas.width - 350, 100, 280, 200);
      // innergrey color
      sceneCtx.fillStyle = "#3f3e43";
      sceneCtx.fill();
      sceneCtx.fillRect(canvas.width - 350, 100, 280, 200);

      //circle for board
      sceneCtx.beginPath();
      sceneCtx.arc(canvas.width - 210, 40, 7, 0, Math.PI * 2, false);
      sceneCtx.fillStyle = "#bdbec2";
      sceneCtx.lineWidth = 2;
      sceneCtx.fill();

      //left line to circle
      sceneCtx.strokeStyle = "black";
      // sceneCtx.shadowBlur = 10;
      // sceneCtx.shadowColor = "gray";
      sceneCtx.beginPath();
      sceneCtx.moveTo(canvas.width - 216, 40);
      sceneCtx.lineTo(canvas.width / 2 + 430, 80);
      sceneCtx.stroke();

      //right line to circle
      sceneCtx.strokeStyle = "black";
      // sceneCtx.shadowBlur = 5;
      // sceneCtx.shadowColor = "gray";
      sceneCtx.beginPath();
      sceneCtx.moveTo(canvas.width - 203, 40);
      sceneCtx.lineTo(canvas.width / 2 + 590, 80);
      sceneCtx.stroke();
      sceneCtx.closePath();

      //text on board
      sceneCtx.strokeStyle = "white";
      // sceneCtx.shadowColor = "white";
      // sceneCtx.shadowBlur = -1;
      sceneCtx.fillStyle = "white";
      sceneCtx.font = "20px serif";
      sceneCtx.fillText("TO DO", canvas.width - 310, canvas.height - 640, 140);
      sceneCtx.closePath();

      sceneCtx.fillText("DONE", canvas.width - 164, canvas.height - 640, 140);
      sceneCtx.closePath();

      //straight line between to do and done
      sceneCtx.strokeStyle = "white";
      sceneCtx.beginPath();
      sceneCtx.moveTo(canvas.width - 210, canvas.height - 650);
      sceneCtx.lineTo(canvas.width / 2 + 510, canvas.height - 500);
      sceneCtx.stroke();
      sceneCtx.closePath();
    }

    //small chits on board logic
    function smallRectOnBoard(x, y, width, height, color, text) {
      this.x = x;
      this.y = y;
      this.width = width;
      this.height = height;
      this.color = color;
      this.text = text;
      this.draw = function() {
        c.fillStyle = this.color;
        c.fillRect(this.x, this.y, this.width, this.height);
        c.fillStyle = "red";
        c.font = "5pt sans-serif";
        c.fillText(this.text, this.x + 5, this.y + 16);
      };
    }

    //small chits throughout the screen
    function smallRect(x, y, width, height, dx, dy, color, text) {
      // width The rectangle's width. Positive values are to the right, and negative to the left.
      // height The rectangle's height. Positive values are down, and negative are up.
      this.x = x;
      this.y = y;
      this.width = width;
      this.height = height;
      this.color = color;
      this.dx = dx;
      this.dy = dy;
      this.text = text;

      this.draw = function() {
        c.fillStyle = this.color;
        c.fillRect(this.x, this.y, this.width, this.height);
        // draw font in red
        c.fillStyle = "black";
        c.font = "8pt sans-serif";
        c.fillText(this.text, this.x + 5, this.y + 25);
      };
      this.update = function() {
        var diagnol = getDiagnol(this.width, this.height) / 2;
        if (this.x + diagnol >= innerWidth || this.x - diagnol <= 0) {
          this.dx = -this.dx;
        }
        if (this.y + diagnol >= innerHeight || this.y - diagnol + this.y <= 0) {
          this.dy = -this.dy;
        }
        this.x += this.dx;
        this.y += this.dy;
        this.draw();
      };
    }

    //users invisible head
    function semiCircle(x, y, radius, color, radian) {
      this.x = x;
      this.y = y;
      this.color = color;
      this.radius = radius;
      this.radian = radian;
      this.draw = function() {
        c.beginPath();
        c.arc(this.x, this.y, this.radius, 0, 0, true);
        c.fillStyle = "";
        c.lineWidth = 2;
        c.fill();
      };
      this.update = function() {
        this.draw();
      };
    }

    function incrementNumber() {
      // console.log("number", tasks);
      sceneCtx.fillStyle = "white";
      sceneCtx.font = "26px Verdana";
      sceneCtx.fillText("", canvas.width - 150, canvas.height - 560);
      sceneCtx.fillText(tasks, canvas.width - 150, canvas.height - 560);
    }

    //on collide create small circles with different velocities.
    function miniCircle(x, y, dx, dy, radius, color) {
      this.x = x;
      this.y = y;
      this.color = color;
      this.dx = dx;
      this.dy = dy;
      this.radius = radius;
      this.velocity = {
        x: randomIntFromRange(-5, 5),
        y: randomIntFromRange(-15, 15)
      };
      this.ttl = 150;
      this.friction = 0.8;
      this.gravity = 0.5;
      this.draw = function() {
        c.beginPath();
        c.fillStyle = color;
        c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
        c.fill();
        c.closePath();
      };
      this.update = function() {
        this.draw();
        if (this.y + this.radius + this.velocity.y > canvas.height) {
          this.velocity.y = -this.velocity.y * this.friction;
        } else {
          this.velocity.y += this.gravity;
        }
        this.x += this.velocity.x;
        this.y += this.velocity.y;
        this.ttl -= 1;
      };
    }

    //Variable Declaration
    let circle1;
    let miniCircleArray;
    let miniRectArray;
    let rectOnBoard;
    let number = 0;
    function getRandomCircleValues() {
      var radius = 40;
      // var x = Math.random() * (innerWidth - radius * 2) + radius;
      // var y = Math.random() * (innerHeight - radius * 2) + radius;
      var x = canvas.width - 360;
      var y = 180;
      var dx = Math.random() - 0.5 * 6;
      var dy = Math.random() - 0.5 * 6;
      var j = Math.floor(Math.random() * code.length);
      var color = randomColors(colorArray);
      var text = code[j];
      var obj = {
        x: x,
        y: y,
        dx: dx,
        dy: dy,
        j: j,
        radius: radius,
        text: text,
        color: color
      };
      return obj;
    }

    function init() {
      miniCircleArray = [];
      miniRectArray = [];
      rectOnBoard = [];
      circle1 = new semiCircle(
        canvas.width / 2,
        canvas.height / 2 + 100,
        80,
        "blue",
        0.9 * Math.PI
      );

      for (let i = 0; i < code.length; i++) {
        var random1 = getRandomCircleValues();
        miniRectArray.push(
          new smallRect(
            random1.x,
            random1.y,
            50,
            50,
            random1.dx,
            random1.dy,
            random1.color,
            random1.text
          )
        );
      }
      for (let i = 0; i < 20; i++) {
        var x = randomIntFromRange(canvas.width - 335, canvas.width - 242);
        var y = randomIntFromRange(canvas.height - 630, canvas.height - 500);
        var randomcolor = getRandomCircleValues();
        rectOnBoard.push(
          new smallRectOnBoard(
            x,
            y,
            30,
            30,
            randomcolor.color,
            randomcolor.text
          )
        );
      }
    }
    function getDiagnol(length, width) {
      return Math.sqrt(Math.pow(length, 2) + Math.pow(width, 2));
    }
    function getDistance(x1, y1, x2, y2) {
      let xDistance = x2 - x1;
      let yDistance = y2 - y1;
      return Math.sqrt(Math.pow(xDistance, 2) + Math.pow(yDistance, 2));
    }

    function animate() {
      requestAnimationFrame(animate);
      c.clearRect(0, 0, innerWidth, innerHeight);
      circle1.update();
      for (var i = 0; i < miniRectArray.length; i++) {
        if (
          getDistance(
            circle1.x,
            circle1.y,
            miniRectArray[i].x,
            miniRectArray[i].y
          ) <
          circle1.radius +
            getDiagnol(miniRectArray[i].width, miniRectArray[i].height)
        ) {
          tasks = tasks + 1;
          sceneCtx.closePath();
          sceneCtx.fillStyle = "#3f3e43";
          sceneCtx.clearRect(canvas.width - 150, canvas.height - 579, 40, 30);
          sceneCtx.fillRect(canvas.width - 150, canvas.height - 579, 40, 30);

          incrementNumber();
          for (var j = 0; j < 1; j++) {
            var circle = miniRectArray[i];
            var color = randomColors(colorArray);
            miniCircleArray.push(
              new miniCircle(circle.x, circle.y, circle.dx, circle.dy, 5, color)
            );
          }
          miniRectArray.splice(i, 1);
          rectOnBoard.splice(i, 1);

          var randomval = getRandomCircleValues();
          miniRectArray.push(
            new smallRect(
              randomval.x,
              randomval.y,
              50,
              50,
              randomval.dx,
              randomval.dy,
              randomval.color,
              randomval.text
            )
          );
          var x = randomIntFromRange(canvas.width - 335, canvas.width - 242);
          var y = randomIntFromRange(canvas.height - 630, canvas.height - 500);
          var random2 = getRandomCircleValues();
          rectOnBoard.push(
            new smallRectOnBoard(x, y, 30, 30, random2.color, random2.text)
          );
        } else {
        }
      }

      rectOnBoard.forEach(boardrect => {
        boardrect.draw();
      });

      miniRectArray.forEach(rect => {
        rect.update();
      });

      miniCircleArray.forEach((minicircle, index) => {
        minicircle.update();
        if (minicircle.ttl === 0) {
          miniCircleArray.splice(index, 1);
        }
      });
    }
    init();
    animate();
    drawBoard();
    incrementNumber();
  }

  render() {
    return (
      <div id="container">
        <canvas className="canvas" style={{ zIndex: 5 }} id="canvas1" />
        <canvas className="canvas" id="scene" />
      </div>
    );
  }
}
export default Home;



